import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'MainPage',
      component: () => import("@/pages/MainPage/MainPage"),
      redirect: '/zksdata',
      children: [
        {
          path: '/zksdata',
          name: 'Analysis',
          component: () => import("@/pages/Zksync/Analysis"),
        },
        {
          path: '/layerzero',
          name: 'Layerzero',
          component: () => import("@/pages/Layerzero/Layerzero"),
        },
        {
          path: '/zksync',
          name: 'Zksync',
          component: () => import("@/pages/Zksync/Zksync"),
        },
        {
          path: '/starknet',
          name: 'Stark',
          component: () => import("@/pages/Stark/Stark"),
        },
        {
          path: '/tools',
          name: 'Tools',
          component: () => import("@/pages/Tools/Tools"),
        },
        {
          path: '/donate',
          name: 'Coffee',
          component: () => import("@/pages/Coffee/Coffee"),
        },
        {
          path: '/random',
          name: 'Random',
          component: () => import("@/pages/Tools/Random"),
        },
        {
          path: '/wallet',
          name: 'Wallet',
          component: () => import("@/pages/Tools/Wallet"),
        },
        {
          path: '/balance',
          name: 'Balance',
          component: () => import("@/pages/Tools/Balance"),
        }
      ]
    }
  ]
})



